import API from '../Api.json';
import Fetch from 'isomorphic-unfetch';

//'https://api.unsplash.com/photos/?client_id=fa2524665e8e782bb4cfc1947a243daa7fc789ce4019748b67b60c6d661e5629
class Weatherwidget extends React.Component{

  state = {
    image: undefined
  }

  componentDidMount(){
    this.getImage();
  }

  getImage = async() => {
    const key = API['Access Key'];
    console.log(key);
    fetch('https://api.unsplash.com/photos/?client_id='+key)
    .then(response => {
      if (response.status == 200) {
        return response;
      } else {
        throw new Error();
      }
    })
    .then((responseData) => {
      this.setState({image:responseData});
      console.log(this.state);
    })
  }

  render(){
    return(
      <div>
      <p>A test text piece</p>
      <img src={this.state.image}/>
      </div>
    );
  }
}

export default Weatherwidget;
